<template>
  <div class="page page-work">
    <div class="pagewrap ">
      <div class="work">
        <a ref="work" target="_blank" :href="site.url" v-for="(site, index) in websites" class="content-block" :key="site+index">
          <div class="content-wrapper" :style="{ 'background-image': 'url(' + site.image + ')' }" :class="site.modifer">
            <div class="title">
              <p>{{site.title}}</p>
            </div>
          </div>
        </a>
      </div>
    </div>
  </div>
</template>
<script>
let WMD = require("../assets/images/wmd.jpg");
let BGC = require("../assets/images/BGC.jpg");
let mercy = require("../assets/images/mercy.jpg");
let map = require("../assets/images/map.jpg");
let carterknifeco = require("../assets/images/carterknifeco.jpg");
let margherita = require("../assets/images/margherita.jpg");
let discogs = require("../assets/images/discogs.jpg");
let hyperdrive = require("../assets/images/hyperdrive.jpg");
let resume = require("../assets/images/resume.jpg");
let plaid = require("../assets/images/plaid.jpg");
let bake = require("../assets/images/bake.jpg");
let cream = require("../assets/images/cream.jpg");
import vue from "Vue";
export default {
  data() {
    return {
      websites: [
        {
          title: "Wild Men Die",
          url: "http://wildmendie.com/",
          image: WMD
        },
        {
          title: "Boys and Girls Club of Greater Houston",
          url: "http://bgcgh.org/",
          image: BGC,
          modifer: "align-top"
        },

        {
          title: "Mercy Health",
          url: "https://blog.mercy.com/",
          image: mercy,
          modifer: "align-top"
        },
        {
          title: "Norton Advertising",
          url: "http://www.norton-outdoor.com/browse-inventory",
          image: map,
          modifer: "align-top"
        },
        {
          title: "Carter Knife Co.",
          url: "http://www.carterknifeco.com/#/",
          image: carterknifeco
        },
        {
          title: "Margherita Meats",
          url: "http://www.margheritameats.com/",
          image: margherita
        },
        {
          title: "Discogs Dash",
          url: "http://discogdash.com/#/search",
          image: discogs,
          modifer: "align-top"
        },
        {
          title: "Hyperdrive",
          url: "http://hyperdrivei.com/",
          image: hyperdrive,
          modifer: "align-top"
        },
        {
          title: "Personal Resumes",
          url: "http://ryancarterresume.com/",
          image: resume,
          modifer: "align-top"
        },
        {
          title: "Plaid Room Records",
          url: "http://howtheturntables.com/",
          image: plaid
        },
        {
          title: "Bake!",
          url: "http://wakeandbakesale.com",
          image: bake
          //modifer: "cover-me"
        },
        {
          title: "Creemboards",
          url: "http://creemboards.com/",
          image: cream
        }
      ],
      sixteenByNine: 0.49429
    };
  },
  methods: {
    adjustSizes() {
      let workBlocks = this.$refs.work;
      Object.entries(workBlocks).forEach(([key, value]) => {
        let block = value;
        let width = block.offsetWidth;
        block.style.height = `${width * 0.47}px`;
      });
    }
  },
  mounted() {
    this.adjustSizes();
    this.$nextTick(function() {
      window.addEventListener("resize", this.adjustSizes);
    });
  }
};
</script>


